import React from 'react'
import { Box, Drawer, Button, List, ListItem } from '@mui/material/'
import { NavLink } from 'react-router-dom'
import MenuBookTwoToneIcon from '@mui/icons-material/MenuBookTwoTone'

export const DrawerPage = () => {
  const [state, setState] = React.useState({ left: false })

  const toggleDrawer = (anchor, open) => (event) => {
    if (event.type === 'keydown' && (event.key === 'Tab' || event.key === 'Shift')) {
      return
    }

    setState({ ...state, [anchor]: open })
  }

  const list = (anchor) => (
    <Box
      sx={{ width: anchor === 'top' || anchor === 'bottom' ? 'auto' : 250 }}
      role="presentation"
      onClick={toggleDrawer(anchor, false)}
      onKeyDown={toggleDrawer(anchor, false)}
    >
      <List>
        <ListItem button exact to='/' component={NavLink}>
          Портфолио
        </ListItem>
        <ListItem button to='/pet' component={NavLink}>
          Pet-проект
        </ListItem>
        <ListItem button to='/contacts' component={NavLink}>
          Контакты
        </ListItem>
      </List>
    </Box>
  )

  return (
    <div>
      <Box sx={{ width: '100%', textAlign: 'center' }}>
        <Button onClick={toggleDrawer('left', true)} sx={{ color: 'white' }}>
          <MenuBookTwoToneIcon/>
        </Button>
      </Box>
      <Drawer
        anchor={'left'}
        open={state.left}
        onClose={toggleDrawer('left', false)}
      >
        {list('left')}
      </Drawer>
    </div>
  )
}