import React, { useState } from 'react'
import { Typography, Accordion, AccordionSummary, AccordionDetails } from '@material-ui/core'
import { makeStyles } from '@material-ui/core/styles'
import ExpandMoreIcon from '@mui/icons-material/ExpandMore'

const useStyles = makeStyles((theme) => ({
  root: {
    marginTop: theme.spacing(1)
  },
  stackAc: {
    backgroundColor: '#f5f5d1'
  },
  stackTitle: {
    width: '33%',
    flexShrink: 0
  },
  stackDes: {
    color: '#9d9b9b;'
  }
}))

export const Stack = () => {
  const classes = useStyles()
  const [expanded, setExpanded] = useState(false)

  const handleChange = (panel) => (event, isExpanded) => {
    setExpanded(isExpanded ? panel : false)
  }

  return (
    <div className={classes.root}>
      <Accordion expanded={expanded === 'panel1'} onChange={handleChange('panel1')} className={classes.stackAc}>
        <AccordionSummary
          expandIcon={<ExpandMoreIcon />}
          aria-controls="panel1bh-content"
          id="panel1bh-header"
        >
          <Typography className={classes.stackTitle}>
            Мой стек
          </Typography>
          <Typography className={classes.stackDes}>Используемые технологии</Typography>
        </AccordionSummary>
        <AccordionDetails>
          <Typography>
            html, css, scss, JavaScript(ES 6+)
          </Typography>
        </AccordionDetails>
        <AccordionDetails>
          <Typography>
            React, MUI, Redux, Redux-Thunk, Redux-Saga, Redux-Form, Formik(MultiStepForm,Yup)
          </Typography>
        </AccordionDetails>
        <AccordionDetails>
          <Typography>
            Git
          </Typography>
        </AccordionDetails>
      </Accordion>
    </div>
  )
}